import styled from "styled-components";
import i1 from "../images/icon_08.png"
import i2 from "../images/icon_00.png"
import i3 from "../images/icon_06.png"
import bkMusic from "../media/bk.mp3"
import voiceMusic from "../media/originalVoice.mp3"
import Slider from "@mui/material/Slider";
import { useEffect, useState } from "react";
import { playRecording, startRecording } from "./Rec";

const Container = styled.div`
width: 100%;
max-width: 400px;
margin-top: 10px;
display: flex;
justify-content: center;
align-items: center;
flex-direction: column;
`
const RecordingButtonContainer = styled.div`
@media screen and (min-width: 580px) {
    button:first-of-type {
      display: none;
    }
    button:last-of-type {
        display: none;
      }
  }
`
const Recording = styled.button`
background-image: url(${i1});
background-color: #d80000;
background-size: 55% 55%;
background-position: center;
background-repeat: no-repeat;
border: none;
width: 70px;
height: 70px;
border-radius: 50%;
:hover {
    background-color: #ef0000;
}
`
const VoicePlayeContainer = styled.div`
width: 100%;
display: flex;
justify-content: space-between;
align-items: center;
`
const PlayerButton = styled(Recording)`
background-color: #56b112 ;
background-image: none;
width: 40px;
height: 40px;
position: relative;
:hover {
    background-color: #67c024;
}
div {
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 8px 0 8px 15px;
    border-color: transparent transparent transparent #FFF;
    position: absolute;
    top: 12px;
    right: 11px;
}
`
const VolumeContainer = styled.div`
width: 90px;
font-size: 18px;
margin-top: 1rem; 
p {
    margin-bottom: -10px;
}
`
const Slider1 = styled(Slider)`
.MuiSlider-thumb {
    height: 16px;
    width: 16px;
    background: #fff;
  }
`;
const AudioPlayer = styled.audio`
  display: none;
`;
let timer; // 計時器
// 計算時間
const time = (t) => {
    return (
        Number(t.slice(3, 5)) * 60 +
        Number(t.slice(6, 8)) +
        Number(t.slice(9, 11)) * 0.001
    );
};
export function RecordingButton(props) {
    const { player, setOrder, order, states } = props;
    const [volume_1, setVolume_1] = useState(50)
    const [volume_2, setVolume_2] = useState(50)
    const [volume_3, setVolume_3] = useState(50)
    let isRecording = 0; // 紀錄錄音狀態，0 表示未錄音，1 表示正在錄音，2 播放原音中
    let audioPlayer_current;
    let audioPlayer_bak;
    let audioPlayer_originalVoice; // 音頻元素
    const [pid,setpid]=useState(window.localStorage.getItem("currentProject_id")
    );
    useEffect(() => {
        audioPlayer_current = document.getElementById("audioPlayer_current");
        audioPlayer_bak = document.getElementById("audioPlayer_bak");
        audioPlayer_originalVoice = document.getElementById("audioPlayer_originalVoice");
        if (!order) {
            setOrder(0);
        }
    }, []);
    // 影片計時停止
    const stopVideo = (time) => {
        audioPlayer_bak = document.getElementById("audioPlayer_bak");
        audioPlayer_originalVoice = document.getElementById("audioPlayer_originalVoice");
        setTimeout(() => {
            audioPlayer_bak.pause();
            audioPlayer_originalVoice.pause();
            player.pauseVideo();
            isRecording = 0;
        }, time);
    };

    // 開始錄音
    const handleRecordingStart = () => {
        audioPlayer_bak = document.getElementById("audioPlayer_bak");
        audioPlayer_originalVoice = document.getElementById("audioPlayer_originalVoice");
        let now = order;
        // 判斷是否正在錄音中
        if (isRecording === 0) {
            player.seekTo(time(states[now].startTime));
            audioPlayer_bak.currentTime = time(states[order].startTime)
            audioPlayer_originalVoice.currentTime = time(states[order].startTime)
            // 影片播放
            player.mute(); // 影片靜音
            audioPlayer_bak.play();
            audioPlayer_originalVoice.play();
            player.playVideo();
            isRecording = 1;
            // 判斷是否為最後一段字幕,開始錄音
            if (now === states.length - 1) {
                startRecording(
                    Number(time(states[now].endTime) - time(states[now].startTime)) * 1000
                );
                stopVideo(
                    Number(time(states[now].endTime) - time(states[now].startTime)) * 1000
                );
            } else {
                startRecording(
                    Number(
                        time(states[now + 1].startTime) - time(states[now].startTime)
                    ) * 1000
                );
                stopVideo(
                    Number(
                        time(states[now + 1].startTime) - time(states[now].startTime)
                    ) * 1000
                );
            }
        }
    };
    // 試聽錄音
    const handleListen = () => {
        if (isRecording === 0) {
            playRecording();
            audioPlayer_bak = document.getElementById("audioPlayer_bak");
            audioPlayer_originalVoice = document.getElementById("audioPlayer_originalVoice");
            audioPlayer_bak.currentTime = time(states[order].startTime)
            audioPlayer_originalVoice.currentTime = time(states[order].startTime)
            audioPlayer_bak.play();
            audioPlayer_originalVoice.play();
            player.seekTo(time(states[order].startTime));
            player.mute();
            player.playVideo();
            isRecording = 3;
            stopVideo(
                Number(
                    time(states[order + 1].startTime) - time(states[order].startTime)
                ) * 1000
            );
        }
    };

    function checkLink(subtitleVoiceLink,id,sort) {
        if (subtitleVoiceLink.includes("type")) {
            return process.env.REACT_APP_API_URL +"/subtitle/"+ subtitleVoiceLink + ""
        } else {
            return process.env.REACT_APP_API_URL + subtitleVoiceLink + "/type/test"
        }
    }
    //音量控制
    useEffect(() => {
        audioPlayer_bak = document.getElementById("audioPlayer_current");
        if (audioPlayer_bak) {
            audioPlayer_bak.volume = volume_3 / 100
        }
    }, [volume_3])
    useEffect(() => {
        audioPlayer_bak = document.getElementById("audioPlayer_bak");
        if (audioPlayer_bak) {
            audioPlayer_bak.volume = volume_1 / 100
        }
    }, [volume_1])
    useEffect(() => {
        audioPlayer_originalVoice = document.getElementById("audioPlayer_originalVoice");
        if (audioPlayer_originalVoice) {
            audioPlayer_originalVoice.volume = volume_2 / 100
        }
    }, [volume_2])
    return <Container>
        <RecordingButtonContainer>
            <PreviousButton
                player={player}
                setOrder={setOrder}
                order={order}
                states={states} />
            <Recording title="錄音" onClick={handleRecordingStart} />
            <NextButton
                player={player}
                setOrder={setOrder}
                order={order}
                states={states} />
        </RecordingButtonContainer>
        <VoicePlayeContainer>
            <PlayerButton title="試聽" onClick={handleListen}><div></div></PlayerButton>
            <VolumeContainer>
                <p>背景音</p>
                <Slider1
                    value={volume_1}
                    onChange={(e) => setVolume_1(Number(e.target.value))}
                    sx={{ width: 90, color: "#80c24e", height: 7 }} />
            </VolumeContainer>
            <VolumeContainer>
                <p>原配音</p>
                <Slider1
                    value={volume_2}
                    onChange={(e) => setVolume_2(Number(e.target.value))}
                    sx={{ width: 90, color: "#80c24e", height: 7 }} />
            </VolumeContainer>
            <VolumeContainer>
                <p>新配音</p>
                <Slider1
                    value={volume_3}
                    onChange={(e) => setVolume_3(Number(e.target.value))}
                    sx={{ width: 90, color: "#80c24e", height: 7 }} />
            </VolumeContainer>
        </VoicePlayeContainer>
        <AudioPlayer
            id="audioPlayer_current"
            className="audioPlayer_current"
            src={states ? (order ? checkLink(states[order].subtitleVoiceLink,pid,states[order].sort) : checkLink(states[0].subtitleVoiceLink),pid,0) : ""}
            controls={true}
        />
        <AudioPlayer
            id="audioPlayer_bak"
            src={bkMusic}
            controls={true}
        />
        <AudioPlayer
            id="audioPlayer_originalVoice"
            src={voiceMusic}
            controls={true}
        />
    </Container>

}

const Next = styled.button`
width: 70px;
height: 70px;
border: none;
&:hover {
    filter: drop-shadow(0px 0px 4px rgba(25, 118, 210, 0.6));
  }
background: url(${i3})
${props => props.notShow && `
display: none;
visibility: hidden;
`}
`
const Previous = styled(Next)`
background: url(${i2})
`
export function NextButton(props) {
    const { player, setOrder, order, states } = props;
    const [notShow, setNotShow] = useState(false)
    const handleNextStage = () => {
        if (order < states.length - 1 || order == 0) {
            setOrder(order + 1);
            player.seekTo(time(states[order + 1].startTime));
            player.pauseVideo();
            clearInterval(timer);
        }
    };
    useEffect(() => {
        if (states && order === states.length - 1) {
            setNotShow(true)
        } else {
            setNotShow(false)
        }
    }, [order])
    return <Next title="下一段" onClick={handleNextStage} notShow={notShow} />

}

export function PreviousButton(props) {
    const { player, setOrder, order, states } = props;
    const handlePreviousStage = () => {
        if (order > 0) {
            setOrder(order - 1);
            player.seekTo(time(states[order - 1].startTime));
            player.pauseVideo();
            clearInterval(timer);
        }
    };
    return <Previous title="上一段" onClick={handlePreviousStage} notShow={order === 0} />

}
